<style scoped>
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
header {
  background: #07111b;
}
ul {
  list-style: none;
}

.classify {
  width: 1386px;
  margin: 0 auto;
}
.classify_m {
  display: flex;
  color: #1c1f21;
  padding: 10px 0;
  border-bottom: 1px solid #edf1f2;
}
.active {
  color: #f20d0d;
  font-weight: 700;
  background: rgba(242, 13, 13, 0.06);
  border-radius: 6px;
}
.hd {
  font-size: 14px;
  margin-top: 10px;
  font-weight: bold;
  color: black;
}

.classify_m > ul {
  flex: 19;
  display: flex;
  flex-wrap: wrap;
}
.classify_m > ul > li {
  cursor: pointer;
  width: auto;
  padding: 5px 16px;
  margin: 3px 2px;
}
.main {
  width: 1386px;
  margin: 0 auto;
  padding: 36px 0;
}
.main_contain {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.contain_l {
  width: 300px;
}
.contain_l img {
  border-radius: 16px;

  width: 100%;
}
.top {
  position: relative;
}
.top > span {
  position: absolute;
  bottom: 10px;
  left: 10px;
  color: white;
}
.bottom {
  padding: 16px 8px;
}
.bottom > h5 {
  font-size: 18px;
  font-weight: bold;
}
.bottom > p {
  display: flex;
  justify-content: space-between;
  margin: 8px 0;
}
.bottom > p > span {
  color: #93999f;
  text-align: left;
  font-size: 12px;
}
.bottom > p:nth-child(4) > span:nth-child(1) {
  color: red;
  font-size: 14px;
}
.ivu-page {
  text-align: center;
  margin: 20px 0;
}
</style>
<template>
  <div>
    <header>
      <navs :sum="num" />
    </header>
    <main>
      <navs2 :img="img" :imgs="imgs" />
      <div class="classify">
        <div class="classify_m">
          <span class="hd">方向:</span>
          <ul>
            <li @click="classify(0)" :class="{ active: ind === 0 }">全部</li>
            <li
              @click="classify(item.id)"
              v-for="(item, index) in list"
              :key="index"
              :class="{ active: ind === item.id }"
            >{{ item.name }}</li>
          </ul>
        </div>
        <div class="classify_m">
          <span class="hd">分类:</span>
          <ul>
            <li @click="subclass(0)" :class="{ active: inds === 0 }">全部</li>
            <li
              :class="{ active: inds === item.id }"
              v-for="(item, index) in ganclassify"
              :key="index"
              @click="subclass(item.id)"
            >{{ item.name }}</li>
          </ul>
        </div>
      </div>
      <div class="main">
        <Page :total="total" @on-change="change" :page-size="pagesize" show-total show-elevator />
        <div class="main_contain">
          <div class="contain_l" v-for="(item, index) in ganclassifyall" :key="index">
            <div class="top">
              <img :src="item.cover_src" alt />
              <span>作者:{{ item.author }}</span>
            </div>
            <div class="bottom">
              <h5>{{ item.title }}</h5>
              <p>
                <span>观看数:{{ item.watch }}</span>
                <span>评论数:{{ item.comment }}</span>
              </p>
              <p>{{ item.introduce }}</p>
              <p>
                <span>{{ item.money }}元</span>
                <span @click="collect(item.id, item.title)">加入收藏</span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>
<script>
import navs from "../components/nav";
import navs2 from "../components/nav2";
export default {
  components: {
    navs,
    navs2
  },
  data() {
    return {
      img: "https://www.imooc.com/static/img/course/logo-course.png",
      imgs: "https://www.imooc.com/static/img/course/course-top.png",
      num: 2,
      ind: 0,
      inds: 0,
      list: [], //父分类
      ganclassify: [], //子分类title
      ganclassifyall: [], //分类下的内容
      total: 0, //总数
      page: 1, //默认首页
      pagesize: 24, //展示数
      url: "http://127.0.0.1:3000/api/ganclassifyall" //默认分页传输路径
    };
  },
  methods: {
    //收藏
    collect(id, title) {
      //收藏
      if (window.sessionStorage.getItem("user") == null) {
        return alert("请先登录");
      }
      this.$axios({
        method: "POST",
        url: "http://127.0.0.1:3000/api/page",
        params: {
          pid: id,
          title: title,
          uid: JSON.parse(window.sessionStorage.getItem("user")).id
        }
      }).then(res => {
        // window.console.log(res.data);
        if (res.data.data) {
          alert("收藏成功");
        } else if (res.data.like.msg) {
          alert("已经收藏不要在点");
        } else {
          alert("收藏失败");
        }
      });
    },
    //分页
    change(page) {
      //传输过来的页数
      this.$axios({
        method: "GET",
        url: this.url,
        params: {
          id: this.ind,
          page: page,
          pagesize: this.pagesize
        }
      }).then(res => {
        this.ganclassifyall = res.data.data; //赋值分类下的内容
        this.total = res.data.total;
        page = this.page;
      });
    },
    //子分类内容
    subclass(ganid) {
      //传输过来的zi分类id
      this.inds = ganid;
      if (ganid > 0) {
        this.$axios({
          url: "http://127.0.0.1:3000/api/subclassification",
          params: {
            page: this.page,
            pagesize: this.pagesize,
            id: ganid
          }
        }).then(res => {
          this.ganclassifyall = res.data.data;
          this.total = res.data.total;
          this.ind = res.data.data[0].fid;
          // this.classify(this.ind);
          //内容不够暂时不改
          // this.url=""
        });
      } else {
        this.classify(this.ind);
      }
    },
    //分类内容
    classify(fid) {
      //点击分类子分类全部
      this.inds = 0;
      if (fid > 0) {
        //分类下的子分类title
        this.$axios({
          method: "post",
          url: "http://127.0.0.1:3000/api/list/" + fid
        }).then(res => {
          this.ganclassify = res.data.data; //赋值给子分类
        });
        //分类下子分类内容
        this.ind = fid;
        this.$axios({
          url: "http://127.0.0.1:3000/api/containall",
          params: {
            id: fid, //查询该分类下的子分类内容
            page: this.page,
            pagesize: this.pagesize
          }
        }).then(res => {
          this.ganclassifyall = res.data.data; //赋值给子分类内容
          this.total = res.data.total; //赋值总数
          this.url = "http://127.0.0.1:3000/api/containall"; //修改page分页传输路径
        });
      } else {
        //查看分类全部子分类
        this.class();
        this.ind = fid;
        //查看分类全部内容
        this.all();
        this.url = "http://127.0.0.1:3000/api/ganclassifyall";
      }
    },
    //全部内容
    all() {
      this.$axios({
        method: "GET",
        url: "http://127.0.0.1:3000/api/ganclassifyall",
        params: {
          page: this.page,
          pagesize: this.pagesize
        }
      }).then(res => {
        this.ganclassifyall = res.data.data;
        this.total = res.data.total;
      });
    },
    //全部子分类
    class() {
      this.$axios({
        url: "http://127.0.0.1:3000/api/ganclassify"
      }).then(res => {
        this.ganclassify = res.data.data;
      });
    }
  },
  directives: {},
  mounted() {
    this.$axios({
      url: "http://127.0.0.1:3000/api/",
      headers: {
        authorization: "Bearer " + window.localStorage.getItem("token")
      }
    }).then(res => {
      this.list = res.data.data; //全部分类
    });
    this.class();
    this.all();
  },
  beforeUpdate() {}
};
</script>
