<style  scoped>
.warp {
  width: 1386px;
  margin: 0 auto;
  /* padding-top: 36px; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* border-bottom: 1px solid #edf1f2; */
}
.warp_l {
  font-size: 0;
}
.warp_l img {
  height: 60px;
  margin-right: 20px;
}
.warp_r {
  display: inline-block;
  border-radius: 16px;
  overflow: hidden;
}
.warp_r > input,
.warp_r > button {
  border: none;
  outline: none;
  background: #f3f5f6;
  padding: 10px 16px;
}
</style>
<template>
  <div>
    <div class="warp">
      <div class="warp_l">
        <img :src="img" alt />
        <img :src="imgs" alt />
      </div>
      <div class="warp_r">
        <input
          type="text"
          onkeyup="this.value=this.value.replace(/[, ]/g,'')"
          @keyup.enter="searchs"
          v-model="title"
          placeholder="搜索感兴趣的内容"
        />
        <button @click="searchs">
          <Icon type="ios-search" />
        </button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: [
    "img", "imgs"
  ],
  data() {
    return {
      title: ''
    }
  },
  methods: {
    searchs() {
      this.$router.push({ name: 'search', query: { words: this.title } })
    },

  },
}
</script>
